























import Vue from 'vue';
import { props } from './props';

export default Vue.extend({
  methods: {
    next() {
      this.active = (this.active + 1) % 6;
    },
  },
  data: () => ({
    props,
    active: 1,
    html: `<steps :active="active">
  <step>Step 1</step>
  <step>Step 2</step>
  <step>Step 3</step>
  <step>Step 4</step>
  <step v-tooltip="'Finish'">Step 5</step>
</steps>
<btn @click="next">Next</btn>`,
    js: `export default {
  methods: {
    next() {
      this.active = (this.active + 1) % 6;
    },
  },
  data: () => ({
    active: 1,
  }),
}`,
  }),
});
